	    sp.jsonp.get(sp.config().DOMAIN + sp.config().urls.actions.handle_social_action, handle_params,
	
	      function(res){
	        sp.send('actions.perform.success', { response: res, action: action });
	      },
	      function(res){
	        sp.send('actions.perform.error', { error: res, action: action });
	      })
	
